	module.exports = {"data":{"wordpressPost":{"title":"Bed sharing, obesity and social media: Most read articles in May","content":"<p>&nbsp;</p>\n<p>The most-read article in May was Carpenter and colleagues&#8217; article on bed sharing with non-smoking parents and the risks of SIDS, which received widespread <a href=\"http://www.bbc.co.uk/news/health-22594587\">press coverage</a>. Still proving popular,was Küster <em>et al</em>&#8216;s study highlighting the dangers of consuming over the counter analgesics before running a marathon which was also <a href=\"http://www.guardian.co.uk/lifeandstyle/2013/apr/19/marathon-runners-health-painkillers-researchers\">covered in the press in the run-up to the London marathon last month</a>. Other newly-published papers in the top ten include Hamm <em>et al</em>&#8216;s study of social media use among patients and caregivers and Sørensen <em>et al</em>&#8216;s study on Dabigatran use in Danish atrial fibrillation patients in 2011.</p>\n<table width=\"483\" border=\"1\" cellspacing=\"0\" cellpadding=\"0\">\n<tbody>\n<tr>\n<td valign=\"top\" width=\"50\">Rank</td>\n<td valign=\"top\" width=\"132\">Author(s)</td>\n<td valign=\"top\" width=\"301\">Title</td>\n</tr>\n<tr>\n<td width=\"50\">1</td>\n<td width=\"132\">Carpenter <em>et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/cgi/content/full/3/5/e002299\">Bed sharing when parents do not smoke: is there a risk of SIDS? An individual level analysis of five major case–control studies</a></td>\n</tr>\n<tr>\n<td width=\"50\">2</td>\n<td width=\"132\">Sørensen <em>et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/cgi/content/full/3/5/e002758\">Dabigatran use in Danish atrial fibrillation patients in 2011: a nationwide study</a></td>\n</tr>\n<tr>\n<td width=\"50\">3</td>\n<td width=\"132\">Schmidt <em>et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/cgi/content/full/3/4/e002698\">Obesity in young men, and individual and combined risks of type 2 diabetes, cardiovascular morbidity and death before 55 years of age: a Danish 33-year follow-up study</a></td>\n</tr>\n<tr>\n<td width=\"50\">4</td>\n<td width=\"132\">Hamm <em>et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/cgi/content/full/3/5/e002819\">Social media use among patients and caregivers: a scoping review</a></td>\n</tr>\n<tr>\n<td width=\"50\">5</td>\n<td width=\"132\">Küster <em>et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/cgi/content/full/3/4/e002090\">Consumption of analgesics before a marathon and the incidence of cardiovascular, gastrointestinal and renal problems: a cohort study</a></td>\n</tr>\n<tr>\n<td width=\"50\">6</td>\n<td width=\"132\">Ekström<em> et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/cgi/content/full/3/4/e002688\">Aspirin treatment and risk of first incident cardiovascular diseases in patients with type 2 diabetes: an observational study from the Swedish National Diabetes Register</a></td>\n</tr>\n<tr>\n<td width=\"50\">7</td>\n<td width=\"132\">Katzmarzyk <em>et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/content/2/4/e000828.full\">Sedentary behaviour and life expectancy in the USA: a cause-deleted life table analysis</a></td>\n</tr>\n<tr>\n<td width=\"50\">8</td>\n<td width=\"132\">Yamada <em>et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/cgi/content/full/3/4/e002537\">Male pattern baldness and its association with coronary heart disease: a meta-analysis</a></td>\n</tr>\n<tr>\n<td width=\"50\">9</td>\n<td width=\"132\">Doyle <em>et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/cgi/content/full/3/1/e001570\">A systematic review of evidence on the links between patient experience and clinical safety and effectiveness</a></td>\n</tr>\n<tr>\n<td width=\"50\">10</td>\n<td width=\"132\">Mahtani <em>et al</em></td>\n<td valign=\"top\" width=\"301\"><a href=\"http://bmjopen.bmj.com/cgi/content/full/3/1/e002058\">Can the London 2012 Olympics ‘inspire a generation’ to do more physical or sporting activities? An overview of systematic reviews</a></td>\n</tr>\n</tbody>\n</table>\n<p>&nbsp;</p>\n<p>Most read figures are based on pdf downloads and full text views. Abstract views are excluded.<!--TrendMD v2.4.3--></p>\n"},"site":{"siteMetadata":{"title":"Gatsby Default Starter"}}},"pathContext":{"id":"18e85c1a-c8e6-5131-94df-0404288390e9"}}

